* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: rgba(186, 185, 185, .627);
  background-image: linear-gradient(-100deg, rgba(247, 249, 89, .9) 10%, rgba(250, 230, 75, .8) 90%);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  height: 45px;
  background-color: #4957f7;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-bottom: 10px;
  display: flex;
}

.input-text {
  width: 250px;
  border-radius: 5px;
  font-size: 20px;
}

.button {
  border-radius: 5px;
  font-size: 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  display: flex;
}

.photo-card {
  min-width: 320px;
  border: 2px solid #000;
  border-radius: 10px;
  flex-basis: calc(25% - 20px);
  transition: all .3s;
  overflow: hidden;
}

.photo-card:hover, .photo-card:focus {
  transform: scale(1.05);
}

img {
  object-fit: cover;
  display: block;
}

.info {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.load-more-container {
  justify-content: center;
  display: flex;
}

.load-more {
  width: 200px;
  cursor: pointer;
  background-color: #f38338;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 20px;
}

